"use client"

import {
  EmptyState,
  FavoriteJobsList,
  LoadingState,
} from "@/components/FavoriteJobsStates"
import { useUser } from "@/contexts/UserContext"
import { usePageFavorites } from "@/hooks/usePageFavorites"
import { useEffect } from "react"

export default function FavoritedJobs() {
  const { userId } = useUser()
  const { pageState, loadFavorites, handleUnfavorite } =
    usePageFavorites(userId)

  useEffect(() => {
    loadFavorites()
  }, [loadFavorites])

  if (pageState.status === "loading") {
    return <LoadingState />
  }

  if (pageState.status === "no-favorites") {
    return <EmptyState />
  }

  return (
    <FavoriteJobsList
      favorites={pageState.favorites}
      onUnfavorite={handleUnfavorite}
    />
  )
}
